
.aboutTop {
    width: 100%;
    height: 10%;
     text-align: center;
     color: black;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.aboutBottom {
    display: block;
    flex-direction: column;
    height: 5%;
    align-items: center;
    margin-top: 70px;
}

