.about h1 {
   font-weight: 600;
   font-size: 60px;
   text-align: center;
   color: rgb(27, 177, 223);
   height: 30px;
   display: inline-flexbox;
   justify-content: center;
   transform: translateY(-40px);
}

.about h2{
   font-weight: 400;
   font-size: 50px;
   text-align: center;
    color: rgb(27, 177, 223);
   height: 10px;
   display: inline-flexbox;
   justify-content: center;
   transform: translateY(-40px);
}
.about h3 {
    font-weight: 800;
    font-size: 20px;
    text-align: center;
     color: rgb(10, 129, 165);
    height: 70px;
    display: inline-block;
    justify-content: center;
    transform: translateY(-40px);
}